import React from "react";
import ReactDOM from "react-dom";
import data from "./data";
import _ from "lodash";
class Search extends React.Component {
    constructor() {
        super();
        this.state = {
            productList: data.productList,
            showList: data.productList
        }
    }
    search(key) {
        if(key === ""){
            this.setState({showList:this.state.productList});
            return ;
        }
        const showList = _.filter(this.state.productList,function (item) {

            return item.name.indexOf(key) !== -1 || item.series.indexOf(key) !== -1;
        });
        this.setState({ showList: showList })
    }
    render() {
        return (
            <div className="priceTable">
                <div className="searchBar">
                    <input ref="searchKey"/>
                    <button onClick={() => {this.search(this.refs.searchKey.value)}} >搜索</button>
                </div>
                <div className="productList">
                    {
                        this.state.showList.map((item) => {
                            return (
                                <div className="table">
                                    <div className="img">
                                        <img src={`..\\img\\${item.imgName}`}/> 
                                    </div>
                                    <div className="detail">
                                        <div className="series">{item.series}</div>
                                        <div className="name">{item.name}</div>
                                        <div className="price">{item.price}</div>
                                        <div className="intro">{item.intro}</div>
                                    </div>
                                </div>
                            );
                        })
                    }
                </div>
            </div>
        )
    }
}
ReactDOM.render(<Search />, document.getElementById("search"));



